<template>
    <div class="guanwang">
        <img width="100%" src="https://www.lianaizhuli.com/bbdata/baobaoqiming.jpg" />
        <!-- <a href="https://www.lianaizhuli.com/app/app.apk">
            <img class="andorid" v-bind:style="andoridobj" src="../assets/icon_android.png">
        </a> -->
        <img class="xiaochengxuma" v-bind:style="xiaochengxumaobj" src="https://www.lianaizhuli.com/bbdata/baobaoqimingma.jpg" />
        <div class="dibu">CopyRights © 2018-2019 深圳宇子科技有限公司版权所有，禁止非法复制 备案号：<a href="http://www.beian.miit.gov.cn">粤ICP备19161511号</a></div>
    </div>
</template>

<script>
    export default {
        async mounted() {
            this.screenWidth=document.body.clientWidth;
            this.xiaochengxumaobj.top=1920/this.screenWidth*16+'px';
            this.andoridobj.top=1920/this.screenWidth*16+'px';
        },
        async created() {
             document.title = '宝宝起名'
        },
        data() {
            return {
                screenWidth:0,
                xiaochengxumaobj:{
                    position: 'absolute',
                    width:'10%',
                    left:'70%',
                    top:'0%',
                },
                andoridobj:{
                    position: 'absolute',
                    width:'10%',
                    left:'48%',
                    top:'0%',
                },
            }
        },
        methods: {},
    }
</script>

<style lang="scss">
    .guanwang {
        .dibu {
            text-align: center;
            margin-bottom: 50px;
        }
    }
</style>



