<template>
  <div>
    <div class="baobao">
      <div class="vip">
        <div :class="currentTab==1 ? 'month active' : 'month'" @click="swichtab(1)">
          <div :class="currentTab==1 ? 'viptext active' : 'viptext'">{{jiagelist[1].miaoshu}}</div>
          <div :class="currentTab==1 ? 'viptext active' : 'viptext'">￥{{jiagelist[1].jiage}}</div>
        </div>
        <div :class="currentTab==2 ? 'month active' : 'month'" @click="swichtab(2)">
          <div :class="currentTab==2 ? 'viptext active' : 'viptext'">{{jiagelist[2].miaoshu}}</div>
          <div :class="currentTab==2 ? 'viptext active' : 'viptext'">￥{{jiagelist[2].jiage}}</div>
        </div>
      </div>
      <div class="zhifu" @click="weixinzhifu">立即支付</div>
      <div class="tequan">
        会员特权
        <div class="tequanneirong" v-if="currentTab==1">
          <div class="tequantext"><text style="font-weight: bolder;font-size: 38rpx;color:#09BB07;">✔ </text>1、无限次数起名</div>
          <div class="tequantext"><text style="font-weight: bolder;font-size: 38rpx;color:#09BB07;">✔ </text>2、每次检索20条名字并按评分从高到低排序</div>
        </div>
        <div class="tequanneirong" v-if="currentTab==2">
          <div class="tequantext"><text style="font-weight: bolder;font-size: 38rpx;color:#09BB07;">✔ </text>1、无限次数起名</div>
          <div class="tequantext"><text style="font-weight: bolder;font-size: 38rpx;color:#09BB07;">✔ </text>2、AI智能起名，每次检索所有名字并按评分从高到低排列</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from "@/api/index";
  import wx from 'weixin-js-sdk';
  export default {
    async mounted() {
      if (!this.$route.query.code) {
        var href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx2cc1bc5a412d44d2&redirect_uri=http%3A//www.lianaizhuli.com/baobao&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect';
        window.location.href = href;
      } else {
        this.get_baobaojiage();
        const json = await api.getFwhnionid({
          code: this.$route.query.code,
        });
        this.unionid = json.data.unionid;
        this.$route.query.code = false;
        localStorage.setItem('unionid', this.unionid);
      }
    },
    async created() {},
    data() {
      return {
        unionid: '',
        nowtime: 0,
        currentTab: 2,
        paySign_data: {},
        detail: ['非会员', '普通月会员', '高级月会员'],
        jiagelist: [],
      }
    },
    methods: {
      async get_baobaojiage() {
        const json = await api.get_baobaojiage();
        this.jiagelist = json.jiagelist;
        this.$apply();
      },
      swichtab(e) {
        this.currentTab = e;
      },
      tiaoqizhifu() {
        let that = this;
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId": that.paySign_data.appId, //公众号名称，由商户传入     
            "timeStamp": that.paySign_data.timeStamp, //时间戳，自1970年以来的秒数     
            "nonceStr": that.paySign_data.nonceStr, //随机串     
            "package": that.paySign_data.package,
            "signType": that.paySign_data.signType, //微信签名方式：     
            "paySign": that.paySign_data.paySign //微信签名 
          },
          function(res) {
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              that.$message({
                type: 'success',
                message: '支付成功!请到宝宝起名AI小程序给您的宝宝起个一生好运势的名字吧~',
                duration: 10000
              });
            } else {
              that.$message({
                type: 'warning',
                message: '您未授权或支付取消',
                duration: 10000
              });
            }
          });
      },
      async get_prepay_id() {
        var timestamp = Date.parse(new Date());
        if (timestamp - this.nowtime < 3000) {
          return false;
        } else {
          this.nowtime = timestamp;
        }
        const json = await api.get_prepay_id1({
          'unionid': this.unionid,
          'zhifutype': this.currentTab,
          'detail': this.detail[this.currentTab],
        });
        this.paySign_data = json.data;
        this.tiaoqizhifu();
      },
      weixinzhifu() {
        this.get_prepay_id();
      },
    },
  }
</script>

<style lang="scss">
  .baobao {
    text-align: center;
    padding-top: 10px;
    padding-bottom: 50px;
    background: #ffffff;
  }
  .zhifu {
    background: #09BB07;
    padding: 8px;
    color: #ffffff;
    border-radius: 20px;
    margin-top: 28px;
    width: 60%;
    margin-left: 20%;
  }
  .tequan {
    margin-top: 20px;
    color: #000000;
  }
  .tequanneirong {
    margin-top: 20px;
    margin-left: 20px;
    text-align: left;
    .tequantext {
      font-size: 15px;
      color: #000000;
    }
  }
  .vip {
    padding-top: 10px;
    display: flex;
    justify-content: space-around;
  }
  .active {
    background: rgba(9, 187, 7, 0.05);
    color: #09BB07;
  }
  .viptext {
    font-size: 15px;
    text-align: center;
    padding: 5px;
  }
  .month {
    padding: 0px;
    width: 46%;
    border-radius: 10px;
    border: 1px solid #000000;
  }
</style>



