import {
  wxRequestPost
} from '@/api/wxRequest';
import {
  wxRequestPost1
} from '@/api/wxRequest1';
import {
  wxRequestPost2
} from '@/api/wxRequest2';

const openData = 'https://www.lianaizhuli.com/'
const apiMain = openData + 'xcx/'
const apiMain1 = openData + 'baobao/'
const apiMain2 = openData + 'zhangfen/'

//微信的jscode换取sessionKey
const getKechengList = (params) => wxRequestPost(params, apiMain + 'getKechengList');
const getKecheng = (params) => wxRequestPost(params, apiMain + 'getKecheng');
const getFwhnionid = (params) => wxRequestPost(params, apiMain + 'getFwhnionid');
const get_kechengprepay_id = (params) => wxRequestPost(params, apiMain + 'get_kechengprepay_id');
const get_prepay_id = (params) => wxRequestPost(params, apiMain + 'get_prepay_id');
const getJiagelist = (params) => wxRequestPost(params, apiMain + 'getJiagelist');
const get_baobaojiage = (params) => wxRequestPost1(params, apiMain1 + 'get_baobaojiage');
const getBaobaofwhnionid = (params) => wxRequestPost1(params, apiMain1 + 'getBaobaofwhnionid');
const get_prepay_id1 = (params) => wxRequestPost1(params, apiMain1 + 'get_prepay_id');
const getFwhnionid2 = (params) => wxRequestPost2(params, apiMain2 + 'getFwhnionid');
const get_jiage2 = (params) => wxRequestPost2(params, apiMain2 + 'get_jiage');
const get_prepay_id2 = (params) => wxRequestPost2(params, apiMain2 + 'get_prepay_id');
const jieqing_prepay_id = (params) => wxRequestPost2(params, apiMain2 + 'jieqing_prepay_id');
const get_shouquanmalist = (params) => wxRequestPost2(params, apiMain2 + 'get_shouquanmalist');
const get_weifukuan = (params) => wxRequestPost2(params, apiMain2 + 'get_weifukuan');
const tingyongshouquanma = (params) => wxRequestPost2(params, apiMain2 + 'tingyongshouquanma');
const qiyongshouquanma = (params) => wxRequestPost2(params, apiMain2 + 'qiyongshouquanma');
const get_yueshouquanma = (params) => wxRequestPost2(params, apiMain2 + 'get_yueshouquanma');
const get_zhongshenshouquanma = (params) => wxRequestPost2(params, apiMain2 + 'get_zhongshenshouquanma');
export default {
  get_zhongshenshouquanma,
  get_yueshouquanma,
  qiyongshouquanma,
  tingyongshouquanma,
  jieqing_prepay_id,
  get_weifukuan,
  getKechengList,
  getKecheng,
  getFwhnionid,
  getFwhnionid2,
  get_kechengprepay_id,
  get_prepay_id,
  getBaobaofwhnionid,
  get_prepay_id1,
  getJiagelist,
  get_baobaojiage,
  get_jiage2,
  get_prepay_id2,
  get_shouquanmalist,
}



