<template>
  <div>
    <div class="zhangfen">
      <div class="vip">
        <div class="month active">
          <div class="viptext active">{{jiage.miaoshu}}</div>
          <div class="viptext active">￥{{jiage.jiage}}</div>
        </div>
      </div>
      <div class="zhifu" @click="weixinzhifu">立即支付</div>
      <div class="tequan">
        <div class="hang">
          <div class="name">总数</div>
          <div class="renshu">激活数</div>
        </div>
        <div class="hang">
          <div class="name">{{shouquanmalist.length}}</div>
          <div class="renshu">{{jihuoshu}}</div>
        </div>
      </div>
      <div class="tequan">
        授权码列表
        <div class="hang">
          <div class="name">授权码</div>
          <div class="renshu">状态</div>
          <div class="renshuzhanbi">购买时间</div>
          <div class="cishu">激活时间</div>
          <div class="cishuzhanbi">激活人员</div>
        </div>
        <div v-for="item in shouquanmalist" :key="item.goumaishijian">
          <div class="hang">
            <div class="name" v-clipboard:copy="item.shouquanma" v-clipboard:success="onCopy">{{item.shouquanma}}</div>
            <div class="renshu">{{item.zhuangtai}}</div>
            <div class="renshuzhanbi">{{item.goumaishijian}}</div>
            <div class="cishu">{{item.jihuoshijian}}</div>
            <div class="cishuzhanbi">{{item.jihuorenyuan}}</div>
          </div>
        </div>
        <div v-if="shouquanmalist.length==0">
          暂无数据
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from "@/api/index";
  import wx from 'weixin-js-sdk';
  export default {
    async mounted() {
      if (!this.$route.query.code) {
        var href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx2cc1bc5a412d44d2&redirect_uri=http%3A//www.lianaizhuli.com/zhangfen&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect';
        window.location.href = href;
      } else {
        const json0 = await api.get_jiage2();
        this.jiage = json0.jiage;
        const json1 = await api.getFwhnionid2({
          code: this.$route.query.code,
        });
        this.unionid = json1.data.unionid;
        const json2 = await api.get_shouquanmalist({
          unionid: this.unionid,
        });
        this.shouquanmalist = json2.shouquanmalist;
        this.jihuoshu = json2.jihuoshu;
        this.$route.query.code = false;
        localStorage.setItem('unionid', this.unionid);
      }
    },
    async created() {},
    data() {
      return {
        unionid: '',
        nowtime: 0,
        currentTab: 1,
        paySign_data: {},
        detail: ['非会员', '终身授权码'],
        jiage: {},
        shouquanmalist: [],
        jihuoshu: 0,
      }
    },
    methods: {
      onCopy() {
        this.$message.success("授权码已复制到剪切板！")
      },
      async get_shouquanmalist() {
        const json = await api.get_shouquanmalist();
        this.shouquanmalistjiage = json.shouquanmalist;
        this.jihuoshu = json2.jihuoshu;
      },
      tiaoqizhifu() {
        let that = this;
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId": that.paySign_data.appId, //公众号名称，由商户传入     
            "timeStamp": that.paySign_data.timeStamp, //时间戳，自1970年以来的秒数     
            "nonceStr": that.paySign_data.nonceStr, //随机串     
            "package": that.paySign_data.package,
            "signType": that.paySign_data.signType, //微信签名方式：     
            "paySign": that.paySign_data.paySign //微信签名 
          },
          function(res) {
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              that.get_shouquanmalist();
              that.$message({
                type: 'success',
                message: '支付成功!',
                duration: 10000
              });
            } else {
              that.$message({
                type: 'warning',
                message: '您未授权或支付取消',
                duration: 10000
              });
            }
          });
      },
      async get_prepay_id() {
        var timestamp = Date.parse(new Date());
        if (timestamp - this.nowtime < 3000) {
          return false;
        } else {
          this.nowtime = timestamp;
        }
        const json = await api.get_prepay_id2({
          'unionid': this.unionid,
          'zhifutype': this.currentTab,
          'detail': this.detail[this.currentTab],
        });
        this.paySign_data = json.data;
        this.tiaoqizhifu();
      },
      weixinzhifu() {
        this.get_prepay_id();
      },
    },
  }
</script>

<style lang="scss">
  .zhangfen {
    text-align: center;
    padding-top: 10px;
    padding-bottom: 50px;
    background: #ffffff;
  }
  .hang {
    width: 100%;
    height: 50rpx;
    line-height: 50rpx;
    border: 1px solid #000;
    display: flex;
    text-align: center;
    .name {
      width: 20%;
      border-right: 1px solid #000;
    }
    .renshu {
      width: 20%;
      border-right: 1px solid #000;
    }
    .renshuzhanbi {
      width: 20%;
      border-right: 1px solid #000;
    }
    .cishu {
      width: 20%;
      border-right: 1px solid #000;
    }
    .cishuzhanbi {
      width: 20%;
      border-right: 1px solid #000;
    }
  }
  .zhifu {
    background: #09BB07;
    padding: 8px;
    color: #ffffff;
    border-radius: 20px;
    margin-top: 28px;
    width: 60%;
    margin-left: 20%;
  }
  .tequan {
    margin-top: 20px;
    color: #000000;
  }
  .vip {
    padding-top: 10px;
    display: flex;
    justify-content: space-around;
  }
  .active {
    background: rgba(9, 187, 7, 0.05);
    color: #09BB07;
  }
  .viptext {
    font-size: 15px;
    text-align: center;
    padding: 5px;
  }
  .month {
    padding: 0px;
    width: 46%;
    border-radius: 10px;
    border: 1px solid #000000;
  }
</style>



