import Vue from 'vue';
import Router from 'vue-router';

import huiyuan from '@/pages/huiyuan';
import baobao from '@/pages/baobao';
import qiming from '@/pages/qiming';
import zhangfen from '@/pages/zhangfen';
// import daili from '@/pages/daili';
import kechenglist from '@/pages/kechenglist';
import kecheng from '@/pages/kecheng';
import kechengneirong from '@/pages/kechengneirong';
import guanwang from '@/pages/guanwang';
import lianaishenqi from '@/pages/lianaishenqi';
import liaotianhuashu from '@/pages/liaotianhuashu';
import liaotianshenqi from '@/pages/liaotianshenqi';

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: guanwang,
      name: '官网',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/liaotianshenqi',
      component: liaotianshenqi,
      name: '聊天神器',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/liaotianhuashu',
      component: liaotianhuashu,
      name: '聊天话术',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/lianaishenqi',
      component: lianaishenqi,
      name: '恋爱神器',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/huiyuan',
      component: huiyuan,
      name: '会员',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/baobao',
      component: baobao,
      name: '宝宝起名',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/qiming',
      component: qiming,
      name: '宝宝起名',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/zhangfen',
      component: zhangfen,
      name: '恋爱话术授权码',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    // {
    //   path: '/daili',
    //   component: daili,
    //   name: '代理',
    //   meta: {
    //     keepAlive:false,
    //     huancun:false,
    //   },
    // },
    {
      path: '/kechenglist',
      component: kechenglist,
      name: '课程列表',
      meta: {
        keepAlive:true,
        huancun:false,
      },
    },
    {
      path: '/kecheng',
      component: kecheng,
      name: '课程',
      meta: {
        keepAlive:true,
        huancun:false,
      },
    },
    {
      path: '/kechengneirong',
      component: kechengneirong,
      name: '课程内容',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
  ],
});
router.beforeEach((to, from,next) => {
  if (from.path=='/kechenglist'&&to.path=='/kecheng') {
    to.meta.huancun=true;
   }
   else{
    to.meta.huancun=false;
   }
   next()
})

export default router;



